.container{
    color: white;
}
.col{
    min-height: 100%;
}
.card-body{
    text-align: center;
}

.blink_me {
    animation: blinker 1.5s linear infinite;
    
  }

  @keyframes blinker {
    50% {
      opacity: 0;
      
    }
}

.bg-color-1{
    background-color:#ca6497 ;
}  

.bg-color-2{
    background-color:#8b8b37 ;
}

.bg-color-3{
    background-color:#3ba0a3 ;
}

  .card{
    height: 100%;
    width: 100%;
    transition: 0.2s;
  }

  .card:hover {
    height: 105%;
    width: 105%;
    box-shadow: 10rem;
  }